@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: "Orbitron", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

#navbarDiv {
  /* height: 10em; */
  background-color: #ff006a !important;
  margin-bottom: 5em;
  width: 100vw;
}
#navbarContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
#canvas {
  background-color: #ff006a !important;
}
.canvasLink {
  text-decoration: none;
  color: white;
  font-weight: 500;
}
.navLink {
  color: white;
  font-size: 1.3em;
  font-weight: 500;
}
#navbarToggle {
  padding: 2em;
  border: none;
}
#navbarTitle {
  font-weight: 500;
  border: 2px solid black;
  padding: 5px;
  color: white;
  background-color: black;
  margin-left: 2em;
}

#aboutPage {
  width: 100%;
  height: 30em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(58, 6, 107); */
  background-color: #022446;

  color: white;
  padding: 2em;
}
#aboutTitle {
  font-size: 3em;
  color: white;
  border-bottom: 5px solid black;
  margin-bottom: 1em;
  font-weight: 900;
}

.aboutText {
  font-size: 1.3em;
  font-weight: 700;
  margin: 0 2em 0 2em;
  font-weight: 700;
  border: 10px solid #ff006a;
  padding: 1em;
}

#examplesDiv {
  width: 100vw;
  height: 100%;
  border: 2px solid red;
  background-color: #ff006a;
}

.ExampleImageContainer {
  width: 100%;
  height: 55vh;
  background-color: black;
  margin: 0;
  margin-bottom: 1em;
}
.ExampleImageContainerMobile {
  width: 100%;
  height: 35vh;
  background-color: black;
  margin: 0;
}
.ExampleImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
  /* color: white; */
  font-size: 10em;
}

#roadmapComponent {
  height: 80%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ff006a;
  color: white;
  font-weight: 700;
}
#roadmapCompTitle {
  font-size: 5em;
  color: black;
  border-bottom: 2px solid white;
}
.roadmapTextContainer {
  margin-bottom: 3em;
  margin-top: 3em;
  background-color: #ce0e5e;
  padding: 1em;
}
.roadmapTitle {
  margin-bottom: 15px;
  color: black;
  font-size: 3em;
  font-weight: 700;
}
.roadmapText {
}
.roadmapBtn {
  /* background-color: white !important; */
}

#faqComponent {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* margin-bottom: 5em; */
  background-color: #022446;
  color: white;
}
.faqTextContainer {
  margin-bottom: 3em;
  margin-top: 3em;
  background-color: #061d35;
  padding: 1em;
}
#faqCompTitle {
  font-size: 5em;
  color: white;
  border-bottom: 2px solid black;
}

.faqTitle {
  margin-bottom: 15px;
  color: white;
  font-size: 3em;
  font-weight: 700;
}

.faqText {
}

#signUpDiv {
  height: 50%;
  width: 100vw;
  background-color: black;
  color: orange;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#signupForm {
  display: flex;
}
.signupItem {
  margin: 0.5em;
  margin-bottom: 5em;
}
#signupBtn {
  width: 7em;
}


#landingPage {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff006a;
}

#landingImageContainer {
  width: 100%;
  height: 40em;
  background-color: black;
  margin-top: 5em;
}
/* #landingContainer {
  padding: 0;
} */
#landingImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #ff006a;
}
#landingWelcomeGroup {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  margin-top: 2em;
  /* border: 2px solid white; */
}
/* #landingWelcomeText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
} */
#LandingFollows {
  width: 100%;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
#followButtons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.followButton {
  margin: 1em;
}

#twitterBtn {
  width: 20em;
  font-weight: 500;
  background-color: #022446;
  color: white;
  padding: 0.5em;
}
#discordBtn {
  width: 15em;
  font-weight: 500;
  background-color: #022446;
  color: white;
  padding: 0.5em;
}
.mint-container {
  /* border: 2px solid white; */
  /* background-color: #022446; */
  color: white;
  padding-top: 5em;
  font-weight: 700;
  width: 70%;
  height: 50%;
  /* margin-top: 3em; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#connectButton {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffc107;
  font-weight: 900;
  font-size: 2em;
  width: 10em;
}
#mintingButton {
  background-color: #ffc107;
  width: 5em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 900;
  color: black;
  font-size: 2em;
  border: 5px solid white;
  margin-bottom: 2em;
}

